
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import value2name from 'vux/src/filters/value2name.js'

export default {
components: {
XHeader,
ViewBox,
Group,
XAddress,
XButton,
Cell
},
data() {
return {
addressData: ChinaAddressV4Data,
value: [],
showAddress: true
}
},
methods: {
getName(value) {
return value2name(value, ChinaAddressV4Data)
},
logHide(str) {
var address = this.getName([this.value[0]])
var address = address.replace("壮族自治区", "");
var address = address.replace("回族自治区", "");
var address = address.replace("维吾尔自治区", "");
var address = address.replace("自治区", "");
console.log("点击完成后打印", address);
this.$router.push({
path: "/index",
name: '/index',
params: {
address: address
}
});
},
}
}



